import { apiMethods } from '../helper/apiMethods'
import { handlePostRequest } from '../helper/service'

export function useAuthEmail(apiEndPoint: string) {
  function register(
    params: registerParamsTypes,
    onSuccess: Function,
    onFailure: Function
  ) {
    handlePostRequest(
      apiEndPoint,
      apiMethods.register,
      params,
      onSuccess,
      onFailure
    )
  }
  function verify(
    params: verifyParamsTypes,
    onSuccess: Function,
    onFailure: Function
  ) {
    handlePostRequest(
      apiEndPoint,
      apiMethods.verify,
      params,
      onSuccess,
      onFailure
    )
  }
  function login(
    params: loginParamsTypes,
    onSuccess: Function,
    onFailure: Function
  ) {
    handlePostRequest(
      apiEndPoint,
      apiMethods.login,
      params,
      onSuccess,
      onFailure
    )
  }
  function forgotPassword(
    params: forgotPasswordParamsTypes,
    onSuccess: Function,
    onFailure: Function
  ) {
    handlePostRequest(
      apiEndPoint,
      apiMethods.forgotPassword,
      params,
      onSuccess,
      onFailure
    )
  }
  function resetPassword(
    params: resetPasswordParamsTypes,
    onSuccess: Function,
    onFailure: Function
  ) {
    handlePostRequest(
      apiEndPoint,
      apiMethods.resetPassword,
      params,
      onSuccess,
      onFailure
    )
  }

  return { register, verify, login, forgotPassword, resetPassword }
}
