import { useMakePOSTRequest } from '../hooks/useMakePostRequest'

const [postRequest] = useMakePOSTRequest()

export function handlePostRequest(
  apiEndPoint: string,
  methodEndpoint: string,
  params: any,
  onSuccess: Function,
  onFailure: Function
) {  
  postRequest(apiEndPoint,methodEndpoint, params)
    .then((result) => {      
      onSuccess(result)
    })
    .catch((error) => {
      onFailure(error)
    })
}
