import { createScriptFile } from './helper'
import {
  InitParams,
  LoginOptions,
  LoginResponse,
  LogoutResponse
} from './types'

export const SDK_SCRIPT_ELE_ID = 'facebook-jssdk'
const loginOption: LoginOptions = {
  scope: 'public_profile, email'
}
export const UseFacebookLoginClient = () => {
  function getFB() {
    return window.FB
  }
  async function loadSdk() {
    await createScriptFile(
      SDK_SCRIPT_ELE_ID,
      `https://connect.facebook.net/en_US/sdk.js`
    )
  }
  async function initialize(initParams: InitParams) {
    getFB()?.init(initParams)
    await loadSdk()
    window.fbAsyncInit = () => {
      initialize(initParams)
    }
  }

  function logout(callback: (res: LogoutResponse) => void) {
    getFB().logout((res: LogoutResponse) => {
      callback(res)
    })
  }

  async function clientLogin(
    callback: (res: LoginResponse) => void,
    loginOptions = loginOption
  ) {
    await getFB().getLoginStatus((res) => {
      if (res.status === 'connected') {
        callback(res)
      } else {
        getFB().login(
          (res: any) => {
            console.log('res', res)
            if (!res.authResponse) {
              return
            }

            return callback(res.authResponse)
          },
          { ...loginOptions }
        )
      }
    })
  }
  return { initialize, loadSdk, getFB, clientLogin, logout }
}
