import React, { useEffect } from 'react'

import { UseFacebookLoginClient } from './useFacebookLoginClient'
import { FacebookLoginProps, InitParams, LoginOptions } from './types/param.ty'
import { LoginResponse } from './types'

function FacebookLogin(props: FacebookLoginProps) {
  const { initialize, clientLogin, loadSdk } = UseFacebookLoginClient()
  const {
    appId,
    scope = 'public_profile, email',
    children = 'Login with Facebook',
    onSuccess,
    onFail,
    styles,
    images,
    imageWidth
  } = props

  const initParams: InitParams = {
    appId: appId,
    cookie: true,
    xfbml: true,
    version: 'v16.0'
  }
  const loginOptions: LoginOptions = {
    scope
  }
  const init = async () => {
    await loadSdk()
    window.fbAsyncInit = () => {
      initialize(initParams)
    }
  }
  useEffect(() => {
    init()
  }, [])

  const requestLogin = () => {
    clientLogin(
      (res: LoginResponse | any) => {
        console.log(res.authResponse)
        if (!res) {
          onFail && onFail({ status: 'loginCancelled' })
          return
        }
        if (res) {
          onSuccess && onSuccess(res.authResponse)
        }
      },
      { ...loginOptions }
    )
  }

  const handleButtonClick = () => {
    requestLogin()
  }

  return (
    <button type='button' style={styles} onClick={handleButtonClick}>
      {images ? (
        <img src={images} style={{ width: `${imageWidth}px` }} />
      ) : null}
      {children}
    </button>
  )
}
export default FacebookLogin
