export function useMakePOSTRequest() {
  function makePOSTRequest(
    apiEndPoint: string,
    methodEndpoint: string,
    params?: {
      [key: string]: string
    }
  ) {
    return new Promise((resolve, reject) => {
      const requestOptions: any = {
        method: 'POST',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        },
        body: new URLSearchParams(params)
      }

      fetch(apiEndPoint + methodEndpoint, requestOptions)
        .then((res) => res.json())
        .then((response) => {
          if (response.responseCode === 100001) resolve(response.responseData)
          else reject(response.responseMessage)
        })
        .catch((error) => reject(error))
    })
  }
  return [makePOSTRequest]
}
